import React, { useState, useEffect, Component } from 'react';
import './App.css';
//import 'bootstrap/dist/css/bootstrap.min.css';

import Data from './data.js';
import Cart from './Cart';
import Card from './Card';
import Detail from './json-to-firestore/Detail';
import About from './About';
import axios from 'axios';
import firebase from './util/firebase';
import { getDatabase, ref, onValue} from 'firebase/database';
import { Link, Routes, Route, Router, useNavigate, Outlet} from 'react-router-dom';
// Switch is replaced by Routes
import { Navbar,Nav,NavDropdown, Form, Button,FormControl, Container, row } from 'react-bootstrap';
//import {connect} from 'react-redux'

function App (Component) {
  
  const [shoes, setShoes] = useState(Data);
  const navigate =useNavigate()
  useEffect(()=>{
   
    const db = getDatabase();
    const tipRef = ref(db, 'tip');
    onValue(tipRef, (snapshot)=>{
    
    const data = snapshot.val();
    console.log(data);
    
    setShoes([...shoes, (data)])
    console.log(shoes)
    })
   },[])
  
  return (
    
   
    <div className="App">

      <div>
      <Navbar bg="dark" variant="dark">
        <Container>
        <Navbar.Brand href="/">Navbar</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link onClick={()=>{ navigate('/')}}>Home</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/detail')}}>Detail</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/about')}}>About</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/event')}}>Event</Nav.Link>
          <Nav.Link href="#pricing">Pricing</Nav.Link>
        </Nav>
        </Container>
      `</Navbar>
      
      </div>
      
      <Routes>
        <Route path="/" element={
                  
      <>   
      <div className="container">
        <div className="row">
          { 
            shoes.map((a,i)=>{
             return <Card shoes={shoes[i]} i={i} key={i} />
            })
          }
        </div>
      </div>
      </> 
      }/>

       <Route path="detail/:id" element={ <Detail shoes={shoes}/>}/>
       <Route path="*" element={ <div>없는페이지입니다</div>}/>  
       <Route path="/about" element={ <About/>}>  
          <Route path="member" element={<div>멤버임</div>}/> 
          <Route path="location" element={ <About/>}/> 
       </Route> 
       <Route path="/event" element={<EventPage/>}>
          <Route path="one" element={<p>첫 주문시 양배추즙 서비스</p>}></Route>
          <Route path="two" element={<p>생일기념 쿠폰받기</p>}></Route>
      </Route>       
    </Routes>
</div>
  )
}

function EventPage(){
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
} 
export default App;